import QRCode from 'qrcode.react'
import './App.css'
import {useState} from 'react'


export default function Qr(){
  const[text,setText]=useState("Hello")
  const[value,setValue]=useState("Hello")
  const handleOnChange = (event)=> {
setText(event.target.value);

}
  const downloadQRCode = () => {
    const qrCodeURL = document.getElementById('qrCodeEl')
      .toDataURL("image/png")
      .replace("image/png", "image/octet-stream");
    console.log(qrCodeURL)
    let aEl = document.createElement("a");
    aEl.href = qrCodeURL;
    aEl.download = "QR_Code.png";
    document.body.appendChild(aEl);
    aEl.click();
    document.body.removeChild(aEl);
  }
  const btn=()=>{
    setValue(text);
  }
  return(
    <>
        <div className='container dow'>
<div className="form-floating">
  <textarea className="form-control of" style={{borderRadius:'20px'}} placeholder="Leave a comment here" value={text} onChange={handleOnChange} id="floatingTextarea"></textarea>
  <label htmlFor="floatingTextarea">Enter text here</label>
</div>
<div className='center'>
    <button className="btn" style={{color:'black',backgroundColor:'#00B4D8',marginTop:'20px',width:'10rem'}} onClick={btn}> Convert</button>
    </div>
    </div>
    <div className="center damn">
    
    <QRCode className='' id="qrCodeEl" value={value}/>
    </div>
<div className='center'>
 <input
        type="button"
        className="dwn btn"
        value="Download"
        onClick={downloadQRCode}
      />
      </div>
    </>
    );
}